
/* eslint-disable @typescript-eslint/camelcase */
import { Options, setup, Vue } from 'vue-class-component';

import { useAuthentication, useConsultation, useConsultationStatus } from '@/composable';

import { ConsultationStatus } from '@/types/models';

import {
  RCard, RCardActions, RCardSection,
} from '@/ui/molecules';

import {
  RImg,
  RBadge,
  RSeparator,
  RInput,
  RBtn,
  RSkeleton,
} from '@/ui/atoms';

import { DefaultCard } from '@/ui/organisms';

@Options({
  components: {
    RCard,
    RCardActions,
    RCardSection,
    RImg,
    RBadge,
    RSeparator,
    RInput,
    RBtn,
    RSkeleton,
    DefaultCard,
  },
})
export default class SpecialitiesList extends Vue {
  consultation = setup(useConsultation);

  authentication = setup(useAuthentication);

  status = setup(useConsultationStatus);

  consultationType = 'Онлайн';

  get date(): string {
    const { calculatedDateStart } = this.consultation.consultation.data ?? {};

    if (!calculatedDateStart) {
      return '';
    }

    const date = this.$dateFilters.formatDayMonth(calculatedDateStart);
    const time = this.$dateFilters.formatHoursMinutes(calculatedDateStart);

    return `${date} в ${time}`;
  }

  get canCancelConsultation(): boolean {
    const { data } = this.consultation.consultation;

    if (data?.status) {
      const cancelableStatuses = [
        ConsultationStatus.New,
      ];

      return cancelableStatuses.includes(data.status);
    }

    return false;
  }

  get canViewConclusion(): boolean {
    const { consultation, conclusionFile } = this.consultation;
    const { data } = consultation;

    if (data?.conclusionExists && conclusionFile && data?.status) {
      const conclusions = [
        ConsultationStatus.Completed,
      ];

      return conclusions.includes(data.status);
    }

    return false;
  }

  get pathToConclusionFile(): string {
    return `${this.$routePaths.CONSULTATION_CONCLUSION}/${this.consultation.conclusionFile?.fileId}`;
  }

  async cancelConsultation(): Promise<void> {
    await this.consultation.cancelConsultation();

    if (this.consultation.consultationCancel.error) {
      return;
    }

    this.$analyticsUtils.pushEvent('schedule_cancel_done', {
      eventCategory: 'consultation',
      consultation_guid: this.consultation.consultationCancel.data?.id as string,
      doctor_guid: this.consultation.consultationCancel.data?.doctor.id as string,
    });

    this.$router.push({ name: this.$routeNames.CONSULTATION_CANCEL });
  }
}
